import React from 'react';
import { useTheme } from '@material-ui/styles';
import Chart from 'react-apexcharts';
import { DATA_API_ROOT } from './../../store/constant';
import { Box, Card, CardContent, Grid, makeStyles, Typography } from '@material-ui/core';
import { useState, useEffect } from 'react';
const DATA_URL = `${DATA_API_ROOT}/timeseries.min.json`;

const useStyles = makeStyles(() => ({
    content: {
        padding: 0,
        paddingBottom: '0 !important',
    },
}));

const SalesLineCard = (props) => {
    const classes = useStyles();
    const theme = useTheme();

    const { bgColor, footerData, title, vaccinedose } = props;

    const [dataData, setData] = useState([]);

    const getData = async () => {
        await fetch(DATA_URL)
          .then((response) => {
            return response.json();
          })
          .then((json) => {
            setData(json.TT.dates);
          });
    }

    const chartData = {
        type: 'line',
        height: 115,
        options: {
            chart: {
                sparkline: {
                    enabled: true,
                },
            },
            dataLabels: {
                enabled: false,
            },
            colors: ['#fff'],
    
            stroke: {
                curve: 'smooth',
                width: 3,
            },
            tooltip: {
                theme: 'dark',
                fixed: {
                    enabled: false,
                },
                x: {
                    show: false,
                },
                y: {
                    title: {
                        formatter: () => 'First Dose',
                    },
                },
                marker: {
                    show: false,
                }
            },
        },
        series: [
            {
                name: 'First Dose',
                data: [0],
            },
            {
                name: 'Second Dose',
                data: [0],
            },
        ],
    };

    if(dataData.length === undefined) {
        Object.keys(dataData).map((date, index, array) => {
            if(dataData[date].total.firstdosevaccinated !== undefined){
                chartData.series[0].data.push(dataData[date].total.firstdosevaccinated)
                chartData.series[1].data.push(dataData[date].total.seconddosevaccinated)
            }
        })
    }

    let footerHtml;
    if (footerData) {
        footerHtml = footerData.map((item, index) => {
            return (
                <Grid item key={index}>
                    <Box mt={3} mb={3} p={1}>
                        <Grid container direction="column" spacing={1} alignItems="center">
                            <Typography variant="h4">{item.value}</Typography>
                            <Typography variant="subtitle2" color="secondary">
                                {item.label}
                            </Typography>
                        </Grid>
                    </Box>
                </Grid>
            );
        });
    }
    
    useEffect(() => {
        getData();
    }, []);

    return (
        <Card>
            <CardContent className={classes.content}>
                <Box color="#fff" bgcolor={bgColor ? bgColor : theme.palette.primary.main} p={3}>
                    <Grid container direction="column" spacing={1}>
                        <Grid item container justify="space-between" alignItems="center">
                            {title && (
                                <Grid item>
                                    <Typography variant="subtitle1" color="inherit">
                                        {title}
                                    </Typography>
                                </Grid>
                            )}
                            <Grid item>
                                <Grid container alignItems="center">
                                    {vaccinedose && (
                                        <Typography variant="subtitle1" color="inherit">
                                            {vaccinedose}
                                        </Typography>
                                    )}
                                </Grid>
                            </Grid>
                        </Grid>
                        {chartData && (
                            <Grid item>
                                <Chart {...chartData} />
                            </Grid>
                        )}
                    </Grid>
                </Box>
                {footerData && (
                    <Grid container justify="space-around" alignItems="center">
                        {footerHtml}
                    </Grid>
                )}
            </CardContent>
        </Card>
    );
};

export default SalesLineCard;
