import React from 'react';
import { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';

import {
    Card,
    CardHeader,
    Divider,
    Grid,
    CardContent,
    CardActions,
    Typography,
    Button,
} from '@material-ui/core';

import { DATA_API_ROOT, gridSpacing, STATE_NAMES, numDifferentiation, checkNumber } from '../../../store/constant';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

const DATA_URL = `${DATA_API_ROOT}/data.min.json`;

const useStyles = makeStyles((theme) => ({
    table: {
        minWidth: 350,
    },
    row: {
        padding: '1rem',
        paddingLeft: '1.5rem',
        paddingRight: '1.5rem',
    },
    celllg: {
        padding: '1rem',
        paddingLeft: '1.5rem',
        paddingRight: '1.5rem',
        [theme.breakpoints.down('sm')]: {
            display: 'none',
        },
    },
    cellsm: {
        padding: '1rem',
        paddingLeft: '1.5rem',
        paddingRight: '1.5rem',
        [theme.breakpoints.up('sm')]: {
            display: 'none',
        },
    },
    confirmed: {
        color: theme.palette.error.main,
        fontSize: 12,
    },
    recovered: {
        color: theme.palette.success.main,
        fontSize: 12,
    },
    deceased: {
        color: theme.palette.warning.main,
        fontSize: 12,
    },
}));

export default function StateWiseDataCard() {
    const classes = useStyles();

    const [dataTable, setTable] = useState([]);

    const getData = async () => {
        await fetch(DATA_URL)
          .then((response) => {
            return response.json();
          })
          .then((json) => {
            setTable(json);
          });
      }
      
    useEffect(() => {
        getData();
    }, []);

    return (
        <Grid container spacing={gridSpacing}>
            <Grid item xs={12}>
                <Card>
                    <CardHeader
                        title={
                            <Typography component="div" className="card-header">
                                State-wise Data
                            </Typography>
                        }
                    />
                    <Divider />
                    <CardContent className="p-0">
                        <TableContainer>
                            <Table className={classes.table} size="small" aria-label="simple table">
                                <TableHead>
                                    <TableRow>
                                        <TableCell className={classes.row}>State</TableCell>
                                        <TableCell>Confirmed</TableCell>
                                        <TableCell>Active</TableCell>
                                        <TableCell>Recovered</TableCell>
                                        <TableCell>Deceased</TableCell>
                                        <TableCell>Tested</TableCell>
                                        <TableCell>Vaccinated</TableCell>
                                        <TableCell>Population</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {Object.keys(dataTable).map((row, index) => (
                                        <TableRow key={index}>
                                            <TableCell className={classes.celllg}>{STATE_NAMES[row]}</TableCell>
                                            <TableCell className={classes.cellsm}>{row}</TableCell>
                                            <TableCell><div>{parseInt(dataTable[row].total.confirmed).toLocaleString('en-IN')}</div><div className={classes.confirmed}>{checkNumber(parseInt(dataTable[row].delta?.confirmed).toLocaleString('en-IN'))}</div></TableCell>
                                            <TableCell>{(parseInt(dataTable[row].total.confirmed) - parseInt(dataTable[row].total.recovered) - parseInt(dataTable[row].total.deceased)).toLocaleString('en-IN')}</TableCell>
                                            <TableCell><div>{parseInt(dataTable[row].total.recovered).toLocaleString('en-IN')}</div><div className={classes.recovered}>{checkNumber(parseInt(dataTable[row].delta?.recovered).toLocaleString('en-IN'))}</div></TableCell>
                                            <TableCell><div>{parseInt(dataTable[row].total.deceased).toLocaleString('en-IN')}</div><div className={classes.deceased}>{checkNumber(parseInt(dataTable[row].delta?.deceased).toLocaleString('en-IN'))}</div></TableCell>
                                            <TableCell>{numDifferentiation(parseInt(dataTable[row].total.tested))}</TableCell>
                                            <TableCell>{numDifferentiation(parseInt(dataTable[row].total.vaccinated))}</TableCell>
                                            <TableCell>{numDifferentiation(parseInt(dataTable[row].meta.population))}</TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </CardContent>
                    <CardActions className="f-right">
                        <Button variant="text" size="small" color="primary">
                            View all States
                        </Button>
                    </CardActions>
                </Card>
            </Grid>
        </Grid>
    );
}
